@font-face {
    font-family: 'Koulen-Regular';
    src: url('Koulen-Regular.ttf');
}

*{
    box-sizing: border-box;
}

body{
    font-family: 'Koulen-Regular';
    color: #fff;
    background-image: linear-gradient(
        250deg,
        hsl(240deg 100% 20%) 0%,
        hsl(289deg 100% 21%) 0%,
        hsl(315deg 100% 27%) 4%,
        hsl(329deg 100% 36%) 11%,
        hsl(337deg 100% 43%) 22%,
        hsl(357deg 91% 59%) 35%,
        hsl(17deg 100% 59%) 51%,
        hsl(34deg 100% 53%) 69%,
        hsl(45deg 100% 50%) 86%,
        hsl(55deg 100% 50%) 100%
    );
    margin: 0;
    padding: 20px 0;
    min-height: 100vh;
}

#root{
    height: 100vh;
}

.wrapper{
    width: 80%;
    margin: 0 auto;
    min-height: 100vh;
}


.row{
    display: flex;
    flex-wrap: wrap;
}

.calc{
    width: 300px;
    background-color: rgba(255, 255,255, 0.1);
    border: 1px outset rgba(255, 255, 255, 0.2);
    border-radius: 10px;
    box-shadow: -6px 10px 17px -1px rgba(255, 255, 255, 0.2);
    padding: 5px;
    overflow: hidden;
}

div.button{
    width: 24%;
}

.window{
    width: 70%;
    text-align: end;
    font-size: 2.5rem;
    padding-right: 10px;
}

.operation{
    padding-left: 10px;
    width: 30%;
    font-size: 2.5rem;
}

.valid{
    width: 74.6%;
}

.button button, .valid button{
    display: block;
    width: 100%;
    display: block;
    background-color: transparent;
    border: 0;
    padding: 15px 30px;
    font-size: 1.3rem;
    color: #fff;
    font-family: 'Koulen-Regular';
    border: 1px solid transparent;
    border-radius: 10px;
    text-align: center;
    transition: .3s;
}

.button button:hover, .valid button:hover{
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow: 0px 0px 17px -1px rgba(255, 255, 255, 0.2);
}

.valid button:active, .button button:active{
    border: 1px solid rgba(255, 255, 255, 0.5);
    box-shadow: 0px 0px 17px -1px rgba(255, 255, 255, 0.5);
}

.center{
    justify-content: center  !important;
    align-items: center !important;
}